import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps } from 'react-router-dom';

import styles from '../css/profile.module.css';
import { GET } from '../utils/requests';
import { Player } from '../models';

export default function Team(props: RouteComponentProps<any, StaticContext, any> & {
	id: string
}) {

	const [player, setPlayer] = useState(null as any as Player);
	const updateProfile = useCallback(() => GET({ url: '/team/' + props.id })
		.then((p) => setPlayer(new Player(p, p.histories.length, p.setting))), [setPlayer, props.id]);
	useEffect(() => {
		updateProfile();
	});

	const animate = useMemo(() => props.history.location.state && props.history.location.state.from, [props.history.location]);
	if (!player) return null;
	const profile = !player ? {} : {
		'Unique ID': player.id,
		Status: player.active !== false ? 'Active' : 'Inactive',
		Rating: player.rating,
		'Performance rating': player.performanceRating,
		Playing: player.isPlaying
	};

	const total = (Math.max(player.histories.length, 1) - 1 - (player.isPlaying ? 1 : 0)).toString();
	const totalGames = player.histories.reduce((acc, curr) => {
		if (!curr) return acc;
		acc += curr.played;
		return acc;
	}, 0);
	const scores = !player ? {} : {
		Score: player.score.toString() + ' / ' + total,
		'Game score': player.gamePoints.toString() + ' / ' + totalGames.toString(),
		'Expected score': player.expectedScore.toFixed(2),
		'Expected game score': player.expectedGameScore.toFixed(2) + ' / ' + totalGames,
	};
	if (!player.settings.showRatings) {
		delete profile.Rating;
		delete profile['Performance rating'];
	}
	if (!player.settings.displayPoints) {
		delete scores['Game score'];
		delete scores['Expected game score'];
	}


	return (<>
        <div id='main' className={animate ? 'animated-left' : ''}>
			<div className={styles.container}>
				<div className={styles.title}>
					<div className={['container', styles.info].join(' ')}>
						<h1><span className='bold'>{player.name}</span></h1>
						<div>Email: {player.contact?.email}</div>
						<div>Account creation: {new Date(player.createdAt).toString().slice(0, 15)}</div>
					</div>
				</div>
			</div>				
		</div>
	</>)
}